.container {
    position: absolute;
    top: 120px;
    left: 50%;

    width: 348px;
    margin-left: -207px;
    padding: 35px 33px 42px;

    text-align: center;
}

.title {
    margin: 0 0 40px !important;

    font-family: 'Montserrat', sans-serif !important;
    font-size: 26px !important;
    font-weight: 600 !important;
    font-style: normal !important;
}

.button {
    cursor: pointer;

    width: 347px;
    height: 36px;
    padding: 10px;

    font-family: Montserrat, sans-serif;
    font-weight: 300;
    color: rgb(255, 255, 255);
    text-transform: uppercase;

    background-color: rgb(25, 118, 210);
    border: none;
    border-radius: 30px;
}

button:hover {
    background-color: rgb(11, 104, 192);
    box-shadow: rgb(0 0 0 / 20%) 0 3px 1px -2px, rgb(0 0 0 / 14%) 0 2px 2px 0, rgb(0 0 0 / 12%) 0 1px 5px 0;
}

.textBox {
    margin-top: 20px;
    margin-bottom: 25px;

    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    color: gray;
}

.textMessage {
    margin-top: 20px;
    margin-bottom: 25px;

    color: gray;
    text-align: left;
    letter-spacing: inherit;
}

.image {
    top: 30%;
    bottom: 55%;
    left: 586px;

    box-sizing: border-box;
    width: 108px;
}

.error {
    font-family: 'Montserrat', sans-serif !important;
    font-weight: 600 !important;
    color: #B31232;
}